<template>
    <div>
      <v-head :headTitle='listTitle'></v-head>
      <div v-if="hint.show" class="loading">
           <img src="../assets/images/timg.gif">
      </div>
      <div class="search-select">
          <list :subject='searchMovie'></list>
      </div>
    </div>
</template>

<script>
import vHead from '../components/vHead';
import list from '../components/list';
export default {
    components: {
        vHead,
        list
    },
    data() {
            return {
                searchMovie:[],
                hint: {
                    show: true,
                },
                listTitle:''
            };
    },
    mounted() {
        if(this.$route.params.keyword){
            this.listTitle=this.$route.params.keyword;
            this.$http.jsonp('https://api.douban.com/v2/movie/search?q=' + this.listTitle).then(function(response) {
                this.hint.show=false; 
                this.searchMovie = response.data.subjects;
            }, function() {
            });
        }else if(this.$route.query.keyword){
            this.listTitle=this.$route.query.keyword;
            this.$http.jsonp('https://api.douban.com/v2/movie/search?tag=' + this.listTitle).then(function(response) {
                this.hint.show=false; 
                this.searchMovie = response.data.subjects;
            }, function() {
            });
        }
        
    }
  
}
</script>

<style>
.search_inner_box{
    width:5.9rem;
    margin:0 auto;
}
.search_inner{
   width:5.9rem;
   height:0.69rem; 
   margin-top:0.3rem;
   border:1px solid #eee;
   border-radius:4px;
}
.search_btn{
    width:1rem;
    height:0.69rem; 
    line-height:0.69rem; 
    cursor:pointer;
    border-left:1px solid #eee;
}
.search_i_default{
    width:1rem;
    height:0.69rem;
    line-height:0.69rem; 
    border-right:1px solid #eee;
}
.search_i_ipt{
    width:3.6rem;
    height:0.69rem;
}
.fl{
    float:left;
}
.fr{
    float:right;
}
.hot{
    margin-top:0.9rem;
}
.hot_title{
    height:0.3rem;
    border-left:0.08rem solid #48a76d;
    text-align:left;
    line-height:0.3rem;
    padding-left:0.14rem;
}
.hot_search_box{
    margin-top:0.14rem;
}
</style>



