<template>
  <div class="comming">
    <v-head :headTitle='listTitle'></v-head>
    <div v-if="hint.show" class="loading">
       <img src="../assets/images/timg.gif">
    </div>
    <list :subject='commingMovie'></list>
    <v-footer></v-footer>
  </div>
</template>

<script>
import vHead from '../components/vHead';
import list from '../components/list';
import vFooter from '../components/vFooter';
export default {
    components: {
        vHead,
	    list,
	    vFooter
	},
	data() {
            return {
                commingMovie: [],
                hint: {
				    show: true,
				},
                listTitle:'',
            };
    },
    mounted() {
        this.getsubject();
    },
    methods: {
        getsubject() {
                this.$http.jsonp('https://api.douban.com/v2/movie/coming_soon', {}, {
                    headers: {
                    },
                    emulateJSON: true
                }).then(function(response) {   
                        this.hint.show=false;     
                        this.listTitle=response.data.title;                                  
                        this.commingMovie = response.data.subjects;
                }, function(response) {
                    console.log(response)
                });
            },
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.comming{
	height:100%;
}
.loading{
    position:absolute;
	width:24px;
	height:24px;
	top:50%;
	left:50%;
	margin-top:-12px;
	margin-left:-12px;
}
</style>



