<template>
    <div>
      <v-head :headTitle='listTitle'></v-head>
      <div class="search_inner_box">
          <div class="search_inner">
              <div class="search_i_default fl">默认</div>  
              <input type="text" v-model="keyword" class="search_i_ipt" placeholder="请输入电影标题、演员或导演">
              <div class="search_btn fr" @click="search($event)">搜索</div> 
          </div>
          <div class="hot">
              <div class="hot_title"><b>热门搜索</b></div>
              <div class="hot_search_box">
                  <router-link :to="{name: 'search-result', params: {keyword: hotSearch.title}}" class="i_genner" v-for="hotSearch in hotSearchs" v-show="hotSearch.rating.average>=7">{{hotSearch.title}}</router-link>
              </div>
          </div>
      </div>
      </div>
</template>

<script>
import vHead from '../components/vHead';
import list from '../components/list';
export default {
    components: {
        list,
        vHead
    },
    data() {
            return {
                hotSearchs:[],
                keyword: '',
                listTitle:'',
            };
    },
    mounted() {
        this.getsubject();
    },
    methods: {
        getsubject() {
                this.$http.jsonp('https://api.douban.com/v2/movie/in_theaters', {}, {
                    headers: {
                    },
                    emulateJSON: true
                }).then(function(response) {  
                      this.listTitle=response.data.title;                                      
                      this.hotSearchs = response.data.subjects;
                }, function(response) {
                      console.log(response)
                });
        },
        search(ev) {
            this.$router.push({name: 'search-result', params: {keyword: this.keyword}});
        },
    }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.search_inner_box{
    width:5.9rem;
    margin:0 auto;
}
.search_inner{
   width:5.9rem;
   height:0.69rem; 
   margin-top:0.3rem;
   border:1px solid #eee;
   border-radius:4px;
}
.search_btn{
    width:1rem;
    height:0.69rem; 
    line-height:0.69rem; 
    cursor:pointer;
    border-left:1px solid #eee;
}
.search_i_default{
    width:1rem;
    height:0.69rem;
    line-height:0.69rem; 
    border-right:1px solid #eee;
}
.search_i_ipt{
    width:3.6rem;
    height:0.69rem;
}
.fl{
    float:left;
}
.fr{
    float:right;
}
.hot{
    margin-top:0.9rem;
}
.hot_title{
    padding-left: 0.4rem;
    margin-top: 0.2rem;
    margin-bottom: 0.2rem;
    font-size: 16px;
    font-weight: bold;
    color: #444;
    border-left: 0.1rem solid #47a86c;
    text-align:left;
}
.hot_search_box{
    margin-top:0.14rem;
}
</style>



