function remReSize(){
        var w = $(window).width();
        try
        {
        	w = $(parent.window).width();
        }
        catch(ex){
        	
        };
        if(w>1200){w = 1200;};
        $('html').css('font-size',w/(640/100)+'px');
};
remReSize();
$(window).resize(remReSize);
$(document).ready(function() {remReSize();});
for(var i=0;i<3;i++){
  setTimeout(remReSize, 100*i);
};	


