
/**
 * 收藏电影
 * @param  {Function} options.dispatch store对象解构出来的函数，无需手动提供
 */
export const fetchFavorite = ({ commit }, subjectDetails) => commit('FETCH_FAVORITE', subjectDetails);

export const removeFavorite = ({ commit }, subjectDetails) => commit('REMOVE_FAVORITE', subjectDetails);

/**
 * 收藏演员
 * @param  {Function} options.dispatch store对象解构出来的函数，无需手动提供
 */
export const fetchActor = ({ commit }, subjectDetails) => commit('FETCH_ACTOR', subjectDetails);

export const removeActor = ({ commit }, subjectDetails) => commit('REMOVE_ACTOR', subjectDetails);




