import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex)

const state = {
	collecStatu: {
		allId:[],
        allDetails:[],
    },
    collecActor:{
    	allId:[],
        allDetails:[],
    }
};


const mutations = {
	FETCH_FAVORITE(state,subjectDetails){
		state.collecStatu.allDetails.push(subjectDetails);
		state.collecStatu.allId.push(subjectDetails.id);
		
	},
	REMOVE_FAVORITE(state,subjectDetails){		
		const subjectId=state.collecStatu.allId.indexOf(subjectDetails.id);
		state.collecStatu.allDetails.splice(subjectId,1);
		state.collecStatu.allId.splice(subjectId,1);

	},
	FETCH_ACTOR(state,subjectDetails){
		state.collecActor.allDetails.push(subjectDetails);
		state.collecActor.allId.push(subjectDetails.id);
		
	},
	REMOVE_ACTOR(state,subjectDetails){		
		const subjectId=state.collecActor.allId.indexOf(subjectDetails.id);
		state.collecActor.allDetails.splice(subjectId,1);
		state.collecActor.allId.splice(subjectId,1);

	},
	
}

export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters,
  strict: process.env.NODE_ENV !== 'production'
});



