<template>
  <div>
    <v-head :headTitle='listTitle'></v-head>
    <div v-if="hint.show" class="loading">
       <img src="../assets/images/timg.gif">
    </div>
    <list :subject='weeklyMovie'></list>
    <v-footer></v-footer>
  </div>
</template>

<script>
import vHead from '../components/vHead';
import list from '../components/list';
import vFooter from '../components/vFooter';
export default {
    components: {
        vHead,
	    list,
        vFooter
	},
    data() {
            return {
                weeklyMovie: [],
                hint: {
				    show: true,
				},
                listTitle:'',
            };
    },
    mounted() {
        this.getsubject();
    },
    methods: {
        getsubject() {
                this.$http.jsonp('https://api.douban.com/v2/movie/top250', {}, {
                    headers: {
                    },
                    emulateJSON: true
                }).then(function(response) {  
                        this.hint.show=false;                                         
                        this.weeklyMovie = response.data.subjects;
                        this.listTitle=response.data.title;
                }, function(response) {
                        console.log(response)
                });
            },
    }
}
</script>

