<template>
  <div>
        <v-head :headTitle='listTitle'></v-head>
        <div class="favorite_tab">
	        <router-link class="tab" to="/favorite_movie">电影</router-link>
	        <router-link class="tab" to="/favorite_people">人</router-link>
        </div>
        <div v-if="person.allDetails.length > 0">
            <router-link  :to="{name: 'personDetail', params: {id: personFavoriteItem.id}}" v-for="personFavoriteItem in person.allDetails">
                <div class="favorite-person-item">
                    <div class="favorite-person-intro">
                        <div class="favorite-person-cover"><img :src="personFavoriteItem.avatars.medium"></div>
                        <div class="favorite-person-info">
                            <div class="favorite-person-txt favorite-person-name">{{personFavoriteItem.name}}</div>
                            <div class="favorite-person-txt">{{personFavoriteItem.name_en}}</div>
                            <div class="favorite-person-txt">{{personFavoriteItem.born_place}}</div>
                            <div class="favorite-person-txt favorite-person-works">主要作品：
                                <span class="person-works" v-for="personFavoriteWorkItem in personFavoriteItem.works">
                                    {{personFavoriteWorkItem.subject.title}}
                                    <span v-if="personFavoriteWorkIndex !== personFavoriteItem.works.length - 1">
                                        、
                                    </span>
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </router-link>
        </div>
        <div v-else>
            <noData :noText='text'></noData>
        </div>
  </div>
</template>

<script>
import vHead from '../components/vHead';
import noData from '../components/noData';
import { mapGetters } from 'vuex';
export default {
    components: {
        vHead,
        noData
    },
    computed: mapGetters({
        person: 'getCollecActor',
    }),
    data() {
            return {
                listTitle:'收藏',
                text:'演员'
            };
    },
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.favorite_tab{
    display:flex;
    height:0.68rem;
    justify-content:center;
    align-items:center;
    border-bottom:1px solid #eee;
}
.tab{
    flex:1;
    text-align:center;
}
a{
    display:block;
}
.favorite-film-item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.34rem;
}

.favorite-film-cover {
    width: 2.56rem;
    height: 3.58rem;
    box-shadow: 0.08rem 0.08rem 0.25rem rgba(0, 0, 0, .2);
}

.favorite-film-info {
    width: 3rem;
}

.favorite-film-txt {
    margin-bottom: 0.11rem;
    color: #666;
}

.favorite-film-title {
    font-size: 0.34rem;
    color: #333;
}

.favorite-person-item {
    box-sizing: border-box;
    padding: 0.28rem;
    margin: 0.18rem;
    margin-bottom: 0.5rem;
    background-color: #fff;
    border: 1px solid #e1e1e1;
    border-radius: 0.17rem;
    box-shadow: 0 0.08rem 0.25rem rgba(0, 0, 0, .1);
}

.favorite-person-intro {
    display: flex;
    justify-content: space-between;
}

.favorite-person-info {
    width: 2.98rem;
    text-align:left;
}

.favorite-person-time {
    margin-bottom: 0.17rem;
    font-size: 18px;
    text-align: center;
    color: #ccc;
}

.favorite-person-cover {
    width: 2rem;
    height: 2rem;
    margin-bottom: 0.25rem;
    border-radius: 1rem;
    box-shadow: 0 0.17rem 0.5rem rgba(0, 0, 0, .1);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    overflow:hidden;
}
.favorite-person-cover img{
    width:100%;
}

.favorite-person-txt {
    margin-bottom: 0.12rem;
    color: #999;
}

.favorite-person-name {
    font-size: 20px;
    color: #666;
}
.person-works{
    line-height:0.36rem;
}
</style>



