<template>
  <div>
    <div class="search">
       <i class="search_icon iconfont icon-sousuo-sousuo"></i>
       <input class="search_ipt"  @focus="chkvalue(this)" type="text" placeholder="请输入关键字搜索">
    </div>
  </div>
</template>

<script>
export default {
  methods: {
        chkvalue() {
            this.$router.push('/search')
        }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
input{
  border:none;
  outline:none;
}
.search{
  height:0.68rem;
  background-color:#eee;
  width:100%;
  display:flex;
  align-items:center;
  justify-content:center;
  position:relative;
}
.search_ipt{
  width:6.14rem;
  height:0.51rem;
  border:2px;
  background-color:#fff;
  text-align:center;
  
}
.search_icon{
  position:absolute;
  left:0.3rem;
  top:0.2rem;
  color:#bfbfbf;
}

</style>



