<template>
  <div class="inTheather">
    <v-head :headTitle='listTitle'></v-head>
    <div v-if="hint.show" class="loading">
       <img src="../assets/images/timg.gif">
    </div>
    <vSearch></vSearch>
    <rd-swipe :swipe="swipe">
      <div class="rd-swipe-item" style="background-size: 100%" :style="{ 'background-image': `url(${img})` }" v-for="(img, index) in imgs">
      </div>
    </rd-swipe>
    <list :subject='inMovie'></list>
    <v-footer></v-footer>
  </div>
</template>

<script>
import vHead from '../components/vHead';
import list from '../components/list';
import vSearch from '../components/search';
import vFooter from '../components/vFooter';
import rdSwipe from '../components/Slide.vue'
export default {
  components: {
      vHead,
	    list,
      vSearch,
      vFooter,
      rdSwipe
	},
  data() {
        return {
                inMovie: [],
                hint: {
                    show: true,
                },
                listTitle:'',
                swipe: {
                    activeIndex: 0
                },
                contacts: [{
                  text: 'Page 1'
                }, {
                  text: 'Page 2'
                }, {
                  text: 'Page 3'
                }, {
                  text: 'Page 4'
                }],
                imgs: [
                  'https://static.sesine.com/wechat-weapp-movie/images/banner_1.jpg',
                  'https://static.sesine.com/wechat-weapp-movie/images/banner_2.jpg',
                  'https://static.sesine.com/wechat-weapp-movie/images/banner_3.jpg',
                  'https://static.sesine.com/wechat-weapp-movie/images/banner_4.jpg'
                ]
        };
    },
    mounted() {
        this.getsubject();
    },
    methods: {
        getsubject() {
                this.$http.jsonp('https://api.douban.com/v2/movie/in_theaters', {}, {
                    headers: {
                    },
                    emulateJSON: true
                }).then(function(response) { 
                      this.hint.show=false;  
                      this.listTitle=response.data.title;                                        
                      this.inMovie = response.data.subjects;                      
                }, function(response) {
                      console.log(response)
                });
            },
    },
    beforeRouteEnter(to, from, next) {
      if(to.name === 'index'){
          next(vm => {
            vm.$router.push('/in-theaters');
          })
      }
      next();          
  }
  
}
</script>

