<template>
  <div>
      <v-head :headTitle='listTitle'></v-head>
      <div class="favorite_tab">
          <router-link class="tab" to="/favorite_movie">电影</router-link>
          <router-link class="tab" to="/favorite_people">人</router-link>
      </div>
      <div v-if="favorite.allDetails.length > 0">
       <router-link :to="{name:'subject',params:{id:subjectDetails.id}}" class="favorite-film-item" v-for="subjectDetails in favorite.allDetails">
            <img :src="subjectDetails.images.large" class="favorite-film-cover">
            <div class="favorite-film-info">
               <div class="fd-title favorite_title">{{subjectDetails.title}}</div>
               <div class="fd-intro-txt favorite_msg_c">导演：{{subjectDetails.directors[0].name}}</div>
               <div class="fd-intro-txt favorite_msg_c">演员：
                     <span v-for="cast in subjectDetails.casts">
                        {{cast.name}}/
                     </span>
               </div>
               <div class="fd-intro-txt favorite_msg_c">豆瓣评分：<span v-if="subjectDetails.rating.average == 0">暂无评分</span><span v-else>{{subjectDetails.rating.average}}分</span></div>
               <div class="fd-intro-txt favorite_msg_c">上映年份：{{subjectDetails.year}}年</div>
            </div>
        </router-link>
    </div>
    <div v-else>
        <noData :noText='text'></noData>
    </div>
  </div>
</template>

<script>
import vHead from '../components/vHead';
import noData from '../components/noData';
import { mapGetters } from 'vuex';
export default {
   components: {
        vHead,
        noData
  },
  computed: mapGetters({
      favorite: 'getCollecStatu',
  }),
  data() {
            return {
                subjectDetails:{
                     images:{},
                     rating:{},
                     casts:[{
                        name:'',
                     }],
                     directors:[{
                        name:'',
                     }]
                },
                listTitle:'收藏',
                text:'电影'

            };
    },
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.favorite-film-item {
    display: flex;
    justify-content: space-between;
    padding:0.25rem;

}
.favorite-film-cover {
    width:2.56rem;
    height: 3.6rem;
    box-shadow: 0.17rem 0.17rem 0.5rem rgba(0, 0, 0, .2);
}
.favorite-film-info {
    width: 3rem;
}
.favorite_msg_c{
    color:#666;
}
.favorite_title{
    color:#333;
}
.favorite_tab .router-link-active{
    border-bottom:2px solid #48a76d;
}
.favorite_tab a{
    display:block;
    height:0.68rem;
    line-height:0.68rem;
    color:#000;
}
</style>



