<template>
  <div class="hello" >
        <v-head :headTitle='listTitle'></v-head>
        <div>
            <div class="fd-hd">
                <img :src="subjectDetails.images.large" class="fd-head-bg" style="filter: blur(15px);">
                <img :src="subjectDetails.images.large" class="fd-cover">
                <div class="favorite" @click="filmFavorite()" v-if="favorite.allId.indexOf(subjectDetails.id)===-1"><i class="favorite_shoucang iconfont icon-shoucang"></i><span style="margin-left:0.1rem;">收藏</span></div>
                <div class="favorite_active" @click="delFilmFavorite()" v-else><i class="favorite_shoucang iconfont icon-shoucang"></i><span style="margin-left:0.1rem;">已收藏</span></div>
                <div class="movie_msg">
                   <div class="fd-title">{{subjectDetails.title}}</div>
                   <div class="fd-intro-txt">导演：{{subjectDetails.directors[0].name}}</div>
                   <div class="fd-intro-txt">演员：
                         <span v-for="cast in subjectDetails.casts">
                            {{cast.name}}/
                         </span>
                   </div>
                   <div class="fd-intro-txt">豆瓣评分：<span v-if="subjectDetails.rating.average == 0">暂无评分</span><span v-else>{{subjectDetails.rating.average}}分</span></div>
                   <div class="fd-intro-txt">上映年份：{{subjectDetails.year}}年</div>
                </div>
            </div>
            <div class="fd-data">
                <div class="fd-data-item">
                    <div class="fd-data-num">{{subjectDetails.collect_count}}</div>
                    <div class="fd-data-title">看过</div>
                </div>
                <div class="fd-data-item">
                    <div class="fd-data-num">{{subjectDetails.wish_count}}</div>
                    <div class="fd-data-title">想看</div>
                </div>
                <div class="fd-data-item">
                    <div class="fd-data-num">
                        {{subjectDetails.ratings_count}}
                    </div>
                    <div class="fd-data-title">评分人数</div>
                </div>
            </div>
            <div class="fd-bd">
                <div class="fd-bd-title">剧情简介</div>
                <div class="fd-bd-intro">{{subjectDetails.summary}}</div>
                <div class="fd-bd-title">导演/演员</div>
                <div class="fd-bd-person">
                    <div class="fd-bd-person-item">
                        <img class="fd-bd-person-avatar" :src="subjectDetails.directors[0].avatars.medium">
                        <div class="fd-bd-person-name">{{subjectDetails.directors[0].name}}</div>
                        <div class="fd-bd-person-role">导演</div>
                    </div>
                    <div v-for="filmDetailCastItem in subjectDetails.casts">
                        <router-link  :to="{name: 'personDetail', params: {id: filmDetailCastItem.id}}" class="fd-bd-person-item">
                            <img class="fd-bd-person-avatar" :src="filmDetailCastItem.avatars.medium">
                            <div class="fd-bd-person-name">{{filmDetailCastItem.name}}</div>
                            <div class="fd-bd-person-role">演员</div>
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import vHead from '../components/vHead';
export default {
    computed: mapGetters({
        favorite: 'getCollecStatu',
    }),
    components: {
        vHead
    },
    data() {
            return {
                subjectId: '',
                listTitle:'',
                subjectDetails:{
                     id:this.$route.params.id,
                     images:{},
                     rating:{},
                     casts:[{
                        name:'',
                        avatars:{
                           medium:''
                        }
                     }],
                     directors:[{
                        name:'',
                        avatars:{
                           medium:''
                        }
                     }]
                },

            };
    },
   mounted() {
        this.getsubject();
    },
    methods: {
        filmFavorite(){
             this.$store.dispatch('fetchFavorite',this.subjectDetails);          
        },
        delFilmFavorite(){
             this.$store.dispatch('removeFavorite',this.subjectDetails);
        },
        getsubject() {
                this.subjectId = this.$route.params.id;
                this.$http.jsonp('https://api.douban.com/v2/movie/subject/' + this.subjectId, {}, {
                    headers: {
                    },
                    emulateJSON: true
                }).then(function(response) {  
                        console.log(response.data.casts);                                      
                        this.subjectDetails = response.data;
                        this.subjectDetails.directors=response.data.directors;
                        this.listTitle=response.data.title;
                }, function(response) {
                        console.log(response)
                });
        },
        
    } 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.fd-hd,.favorite_hd{
    position: relative;
    width: 100%;
    height: 5.17rem;
    display: flex;
    justify-content: center;
    align-content: center;
    overflow: hidden;
}
.fd-hd:before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 5.17rem;
    background-color: rgba(0, 0, 0, .6);
}

.fd-data {
    display: flex;
    height: 1.34rem;
    justify-content: space-around;
    align-items: center;
    border-bottom: 1px solid #f4f4f4;
}

.fd-data-item {
    width: 33.33%;
    text-align: center;
}

.fd-data-item {
    border-left: 1px solid #eee;
}

.fd-data-item:first-child {
    border-left: none;
}

.fd-data-num {
    font-size: 18px;
    font-weight: 100;
    color: #444;
    color: #999;
    margin-bottom:0.08rem;
}

.fd-data-title {
    color: #999;
}

.fd-bd {
    width:5.9rem;
    margin:0 auto;
}

.fd-bd-title {
    padding-left: 0.4rem;
    margin-top: 0.2rem;
    margin-bottom: 0.2rem;
    font-size: 16px;
    font-weight: bold;
    color: #444;
    border-left: 0.1rem solid #47a86c;
    text-align:left;
}

.fd-bd-intro {
    text-align: justify;
    line-height: 1.5;
    color: #666;
}
.fd-head-bg{
    position: absolute;
    z-index: -2;
    left: 0;
    top: 0;
    width: 100%;
    height: 5.12rem;
    background-size: cover;
    background-position: center;
    transform: scale(1.2);
}
.movie_msg{
    z-index: 2;
    width: 2.73rem;
    margin-top: 0.68rem;
    margin-left: 0.34rem;
    color: #fff;
}
.fd-title {
    margin-bottom: 0.3rem;
    font-size: 18px;
    text-align:left;
    line-height:0.4rem;
}
.fd-intro-txt {
    margin-bottom: 0.16rem;
    color: #eee;
    text-align:left;
    line-height:0.34rem;
    font-size: 13px;

}
.fd-cover {
    z-index: 9999;
    width: 2.56rem;
    height: 3.59rem;
    margin-top: 0.68rem;
    border-radius: 0.04rem;
    box-shadow: 0 15px 75px rgba(255, 255, 255, .3)
}
.favorite{
    position:absolute;
    bottom:0.2rem;
    left:0.9rem;
    width:1.4rem;
    height:0.46rem;
    border-radius:0.2rem;
    border:1px solid #fff;
    display:flex;
    color:#fff;
    align-items:center;
    justify-content:center;
}
.favorite_active{
    position:absolute;
    bottom:0.2rem;
    left:0.9rem;
    width:1.4rem;
    height:0.46rem;
    border-radius:0.2rem;
    border:1px solid #f5ec41;
    display:flex;
    color:#f5ec41;
    align-items:center;
    justify-content:center;
}
.favorite_shoucang{
    font-size:0.38rem;
}
.fd-bd-person {
    display: flex;
    width: 100%;
    height: 4.4rem;
    overflow-x: scroll;
    overflow-y: hidden;
}
.fd-bd-person-item {
    margin-left: 0.17rem;
    text-align: center;
}

.fd-bd-person-item:first-child {
    margin-left: 0;
}
.fd-bd-person-avatar {
    width: 2.4rem;
    height: 3.4rem;
    margin:0.1rem;
}

.fd-bd-person-name {
    color: #666;

}

.fd-bd-person-role {
    color: #999;
    margin-top:0.1rem;
}
</style>



