<template>
  <div class="hello" >
        <v-head :headTitle='listTitle'></v-head>
        <div class="pd-hd">
            <img class="pd-hd-bg" :src="personDetails.avatars.medium" style="filter: blur(0.25rem);">
            <div class="pd-intro">
                <img class="pd-avatar" :src="personDetails.avatars.medium">
                <div class="pd-name-cn">{{personDetails.name}}</div>
                <div class="pd-name-en">{{personDetails.name_en}}</div>
                <div class="pd-address">{{personDetails.born_place}}</div>
            </div>
            <div class="pd-favorite" @click="personFavorite()" v-if="person.allId.indexOf(personDetails.id)===-1"><i class="favorite_shoucang iconfont icon-shoucang"></i><span style="margin-left:0.1rem;">收藏</span></div>
            <div class="pd-favorite_active" @click="delPersonFavorite()" v-else><i class="favorite_shoucang iconfont icon-shoucang"></i><span style="margin-left:0.1rem;">已收藏</span></div>
        </div>
        <div class="pd-bd">
            <div class="pd-work">
                <div v-for="psersonWorkItem in personDetails.works">
                    <div class="pd-work-item">
                        <img class="pd-work-cover" :src="psersonWorkItem.subject.images.large">
                        <div class="pd-work-intro">
                            <div class="pd-work-title">{{psersonWorkItem.subject.title}}</div>
                            <div class="pd-work-roles">担任：{{psersonWorkItem.roles}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import vHead from '../components/vHead';
export default {
    computed: mapGetters({
        person: 'getCollecActor',
    }),
    components: {
        vHead
    },
    data() {
            return {
                personId: '',
                listTitle:'',
                personDetails:{
                   id:this.$route.params.id,
                   avatars:{
                      medium:''
                   }
                },
                personHint:'收藏',

            };
    },
   mounted() {
        this.getsubject();
    },
    methods: {
        personFavorite(){
             this.$store.dispatch('fetchActor',this.personDetails);           
        },
        delPersonFavorite(){
           this.$store.dispatch('removeActor',this.personDetails);
        },
        getsubject() {
                this.personId = this.$route.params.id;
                this.$http.jsonp('https://api.douban.com/v2/movie/celebrity/' + this.personId, {}, {
                    headers: {
                    },
                    emulateJSON: true
                }).then(function(response) {                                     
                        this.personDetails = response.data;
                        this.listTitle=response.data.name;
                }, function(response) {
                        console.log(response)
                });
        },
        
    } 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.pd-hd {
    position: relative;
    width: 100%;
    height: 5.12rem;
    overflow: hidden;
}

.pd-hd-bg {
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 5.12rem;
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
    transform: scale(1.2);
}

.pd-hd:before {
    content: '';
    display: block;
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 5.12rem;
    background-color: rgba(0, 0, 0, .6);
}
.pd-intro {
    position: absolute;
    z-index: 6;
    display: flex;
    width: 100%;
    height: 5.12rem;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    color: #fff;
}

.pd-avatar {
    width: 2.4rem;
    height: 2.4rem;
    border-radius: 2.4rem;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    box-shadow: 0 0 160rpx rgba(255, 255, 255, .6);
}

.pd-name-cn {
    padding: 0.08rem 0.17rem;
    margin-top: 0.26rem;
    background-color: rgba(0, 0, 0, .1);
    border-radius: 0.27rem;
    font-size: 0.27rem;
    box-shadow: 0 0 20rpx rgba(0, 0, 0, .2) inset,1px 1px 0 rgba(255, 255, 255, .1);
}

.pd-name-en {
    margin-top: 0.08rem;
    font-size: 0.2rem;
}

.pd-address {
    margin-top: 0.17rem;
    font-size: 12px;
}

.pd-work {
    box-sizing: border-box;
    display: flex;
    width: 100%;
    padding: 0.08rem;
    margin-top: 0.08rem;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
}

.pd-work-item {
    width: 3rem;
    height:6rem;
    overflow: hidden;
    text-align: center;
    background-color: #f8f8f8;
    border: 1px solid #eee;
    margin-bottom: 0.13rem;
}

.pd-work-cover {
    width: 3rem;
    height: 4.5rem;
}

.pd-work-intro {
    box-sizing: border-box;
    padding:6px;
}

.pd-work-title {
    font-size: 18px;
    line-height:0.4rem;
    color: #333;
}

.pd-work-roles {
    color: #666;
    margin-top:0.1rem;
}
.pd-favorite {
    box-sizing: border-box;
    position: absolute;
    z-index: 99;
    top: 0.25rem;
    right: 0.25rem;
    width: 1.4rem;
    height: 0.46rem;
    border-radius: 0.2rem;
    border: 1px solid #fff;
    display: -ms-flexbox;
    display: flex;
    color: #fff;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: center;
    justify-content: center;
}
.pd-favorite_active{
    box-sizing: border-box;
    position: absolute;
    z-index: 99;
    top: 0.25rem;
    right: 0.25rem;
    width: 1.4rem;
    height: 0.46rem;
    border-radius: 0.2rem;
    border: 1px solid #f5ec41;
    display: -ms-flexbox;
    display: flex;
    color: #f5ec41;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: center;
    justify-content: center;
}

</style>



