const routes = [{
   path: '/',
   name: 'index',
   component:require('./view/in-theaters'),

},{
   path: '/in-theaters',
   name: 'in-theaters',
   component:require('./view/in-theaters'),

},{
   path: '/coming-soon',
   name: 'coming-soon',
   component:require('./view/coming-soon'),

},{
   path: '/weekly',
   name: 'weekly',
   component:require('./view/weekly'),

},{
   path: '/my',
   name: 'my',
   component:require('./view/my'),

},{
   path: '/search',
   name: 'search',
   component:require('./view/search')
},{
   path: '/search-result/:keyword',
   name: 'search-result',
   component:require('./view/search-result')
},{
   path: '/subject/:id',
   name: 'subject',
   component:require('./view/subject')
},{
   path: '/favorite_movie',
   name: 'favorite_movie',
   component:require('./components/favorite_movie')
},{
   path: '/favorite_people',
   name: 'favorite_people',
   component:require('./components/favorite_people')
},{
   path: '/personDetail/:id',
   name: 'personDetail',
   component:require('./view/personDetail')
}];


export default routes;


