<template>
  <div class="foot_menu">
      <router-link class="menu" :to="tab.path" v-for="tab in items">
      <div v-html="tab.fontIcon"></div>
        <div class="foot_menu_name">{{ tab.name }}</div>
      </router-link>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      items: [
        {
          name: '热映',
          path: '/in-theaters',
          fontIcon:'<i class="foot_icon iconfont icon-fangying"></i>',
        },
        {
          name: '即将上映',
          path: '/coming-soon',
          fontIcon:'<i class="foot_icon iconfont icon-sytx"></i>',
        },
        { 
          name: '口碑榜',
          path: '/weekly',
          fontIcon:'<i class="foot_icon iconfont icon-paiming"></i>',
        },
        { 
          name: '我的',
          path: '/my',
          fontIcon:'<i class="foot_icon iconfont icon-iconfontwohover"></i>',
        }
      ],
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.foot_menu{
  display:flex;
  position:fixed;
  bottom:0;
  width:100%;
  height:0.88rem;
  align-items:center;
  background-color:#fff;
  z-index:999;

}
.menu{
  flex:1;
  color: #666;
}
.active{
  background-image: url(../assets/images/my_active_icon.png);
}
.foot_menu .router-link-active{
  color:#48a76d;
}
.foot_menu_name{
  margin-top:0.1rem;
  font-size:0.2rem;
}
.foot_icon{
  font-size:0.32rem;
}
</style>



